.plans-page .section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}
.plans-page .section-header h3 {
  font-size: 1rem;
  font-weight: 700;
  color: #111827;
  margin: 0;
}
.plans-page .section-header p {
  color: #6b7280;
  margin: 0.25rem 0 0;
}
.plans-page .section-header .actions {
  display: flex;
  gap: 0.5rem;
}
.plans-page .metrics-grid {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 0.75rem;
  margin-bottom: 1rem;
}
.plans-page .metrics-grid .metric-card {
  position: relative;
  background: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 0.75rem;
  padding: 1rem;
}
.plans-page .metrics-grid .metric-card .label {
  color: #6b7280;
  font-size: 0.875rem;
}
.plans-page .metrics-grid .metric-card .value {
  font-size: 1.5rem;
  font-weight: 700;
}
.plans-page .metrics-grid .metric-card .value.success {
  color: #16a34a;
}
.plans-page .metrics-grid .metric-card .value.danger {
  color: #ef4444;
}
.plans-page .metrics-grid .metric-card .icon {
  position: absolute;
  right: 0.75rem;
  top: 0.75rem;
  opacity: 0.6;
}
.plans-page .skeletons {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.75rem;
}
.plans-page .empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 0.75rem;
  padding: 3rem;
  color: #6b7280;
}
.plans-page .empty-state .title {
  font-weight: 600;
  color: #111827;
  margin-top: 0.5rem;
}
.plans-page .empty-state .subtitle {
  margin: 0.25rem 0 1rem;
}
.plans-page .cards-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.75rem;
}
.plans-page .plan-card {
  background: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 0.75rem;
  padding: 1rem;
}
.plans-page .card-title {
  font-weight: 600;
}
.plans-page .card-subtitle {
  color: #6b7280;
  font-size: 0.875rem;
}
.plans-page .card-meta {
  margin-top: 0.25rem;
  font-size: 0.875rem;
}
.plans-page .btn {
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  border: none;
}
.plans-page .btn.btn-secondary {
  background: white;
  color: #374151;
  border: 1px solid #d1d5db;
}
.plans-page .btn.btn-secondary:hover {
  background: #f9fafb;
  border-color: #9ca3af;
}
.plans-page .btn.btn-primary {
  background: #2563eb;
  color: white;
}
.plans-page .btn.btn-primary:hover {
  background: #1d4ed8;
}

.plan-card-checkbox {
  width: 1.25rem;
  height: 1.25rem;
  color: #2563eb;
  background-color: white;
  border: 2px solid #60a5fa;
  border-radius: 9999px;
  appearance: none;
  -webkit-appearance: none;
  cursor: pointer;
  position: relative;
}

.plan-card-checkbox:checked {
  background-color: #60a5fa;
  border-color: #60a5fa;
}

.plan-card-checkbox:checked::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0.5rem;
  height: 0.5rem;
  background-color: white;
  border-radius: 50%;
}

.plan-card-checkbox:focus {
  outline: none;
  box-shadow: 0 0 0 2px #3b82f6;
}

.plan-list-checkbox {
  width: 1rem;
  height: 1rem;
  color: #2563eb;
  background-color: white;
  border: 2px solid #60a5fa;
  border-radius: 50%;
  appearance: none;
  -webkit-appearance: none;
  cursor: pointer;
  position: relative;
}

.plan-list-checkbox:checked {
  background-color: #60a5fa;
  border-color: #60a5fa;
}

.plan-list-checkbox:checked::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0.375rem;
  height: 0.375rem;
  background-color: white;
  border-radius: 50%;
}

.plan-list-checkbox:focus {
  outline: none;
  box-shadow: 0 0 0 2px #3b82f6;
}

.plan-card {
  transition: all 0.2s ease-in-out;
}

.plan-card:hover {
  transform: translateY(-0.25rem);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.border-blue-400 {
  border-color: #60a5fa;
}

.btn {
  transition: all 0.2s ease-in-out;
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.relative {
  position: relative;
}

.z-50 {
  z-index: 50;
}

@media (max-width: 640px) {
  .grid {
    grid-template-columns: 1fr;
  }
}
@media (min-width: 641px) and (max-width: 768px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 769px) and (max-width: 1024px) {
  .grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1025px) {
  .grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}
.animate-pulse {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

input:focus {
  outline: none;
  box-shadow: 0 0 0 2px #3b82f6;
  border-color: transparent;
}

button:hover:not(:disabled) {
  transform: scale(1.05);
}

